.slider {
    max-width: 700px;
    height: 550px;
    padding: 0 !important;
    position: relative;
    overflow: hidden;
    margin: 2rem auto;
    border-radius: 12px;
    box-shadow: 2px 2px 10px 1px #000;

    &-container {
        width: 100%;
        height: 100%;

    }

    &-item {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 100%;
        opacity: 0;
        transition: opacity .5s ease;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }

        &.active {
            opacity: 1;
            transition: opacity .5s ease;

        }
    }

    &-btn {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 35px;
        height: 35px;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.726);
        cursor: pointer;
        box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.425);

        &:hover {
            background-color: #fff;
        }

        &.next {
            background-image: url('../assets/image/next.png');
            background-repeat: no-repeat;
            background-position-y: center;
            background-position-x: 60%;
            background-size: 70%;
            right: 20px;
        }

        &.prev {
            background-image: url('../assets/image/left-arrow.png');
            background-repeat: no-repeat;
            background-position-y: center;
            background-position-x: 25%;
            background-size: 70%;
            left: 20px;
        }
    }

    &-bullets {
        display: flex;
        align-items: center;
        column-gap: 0.7rem;
        position: absolute;
        bottom: 20px;
        left: 50%;
        transform: translateX(-50%);
    }

    &-bullet {
        cursor: pointer;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.74);
        position: relative;
        box-shadow: 1px 1px 3px #000;
        &::before {
            content: '';
            position: absolute;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            top: 2px;
            left: 2px;
            transform: scale(0);
        }
        &.active {
            transform: scale(1.3);
            background-color: #fff;
            position: relative;

            &::before {
                transform: scale(1);
            }
        }

        &:hover {
            background-color: #fff;
            transition: all 0.3s ease;
        }
    }
}